@import "../font-awesome/fontawesome.scss";
@import "../font-awesome/brands.scss";
@import "../font-awesome/solid.scss";
@import "../font-awesome/regular.scss";

* {
	box-sizing: border-box;
}

html {
	-webkit-text-size-adjust: 100%; // for iOS
	box-direction: normal;
	font-family: $root-font-family;
	font-size: $small-device-font-size;
	line-height: $root-line-height;
	font-weight: $root-font-weight;
}

html, body {
	margin: 0;
	padding: 0;
}

h1,
h2,
h3,
h4 {
	font-weight: $heading-font-weight;
}

h1 {
	font-size: $h1-font-size;
	line-height: $h1-line-height;
	margin-bottom: 1rem;
}

h2 {
	font-size: $h2-font-size;
	margin-bottom: .5rem;
}

h3 {
	font-size: $h3-font-size;
}

h4 {
	font-size: $h4-font-size;
}

h5 {
	font-size: $h5-font-size;
}

figure {
	max-width: 100%;
	margin: 0;
	padding: 0;
}

%quotebox {
	padding: .8889rem;
	margin-top: 1rem;
	margin-left: 0px;
	margin-right: 0px;
	//border-radius: 6px;
	border-left: 6px solid;
	border-right: 6px solid transparent;
	border-right-width: 6px;
	border-right-style: solid;
	border-right-color: rgba(0, 0, 0, 0) transparent;
	color: #7a7a7a;
}

blockquote {
	background-color: #fafafa;
	border-left-color: #e6e6e6;
	@extend %quotebox;
}

.important {
	background-color: #fbf8e8;
	border-left-color: #fee450;
	@extend %quotebox;
}

.warning {
	background-color: #f2dbdc;
	border-left-color: #ae272f;
	@extend %quotebox;
}

div.notices {
	margin: 2rem 0;
	position: relative;
}
div.notices p {
	padding: 15px;
	display: block;
	font-size: 1rem;
	margin-top: 0rem;
	margin-bottom: 0rem;
	color: #666;
}
div.notices p:first-child:before {
	@extend %fa-icon;
	@extend .fas;
	
	position: absolute;
	top: 5px;
	color: #fff;
	font-family: "Font Awesome 5 Free";
	font-weight: 900;
	content: fa-content($fa-var-question-circle);
	left: 10px;
}
div.notices p:first-child:after {
	position: absolute;
	top: 0px;
	color: #fff;
	left: 2rem;
}
div.notices.info p {
	border-top: 30px solid #F0B37E;
	background: #FFF2DB;
}
div.notices.info p:first-child:after {
	content: 'Info';
}
div.notices.warning p {
	border-top: 30px solid rgba(217, 83, 79, 0.8);
	background: #FAE2E2;
}
div.notices.warning p:first-child:after {
	content: 'Warning';
}
div.notices.note p {
	border-top: 30px solid #346896;
	background: #E7F2FA;
}
div.notices.note p:first-child:after {
	content: "Definition";
}
div.notices.tip p {
	border-top: 30px solid rgba(92, 184, 92, 0.8);
	background: #E6F9E6;
}
div.notices.tip p:first-child:after {
	content: 'Focus';
}

kbd {
	font-family: $root-font-family;
	padding: 2px 7px;
	border: 1px solid $gray-4;
	font-size: 0.8em;
	line-height: 1.4;
	background-color: #f3f3f3;
	color: $gray-9;
	box-shadow: 0 0 0 rgba(0, 0, 0, 0.2), 0 0 0 2px $white inset;
	border-radius: 3px;
	display: inline-block;
	text-shadow: 0 1px 0 $white;
	white-space: nowrap;
}

a {
	//border-bottom: 1px dotted;
	color: $link-color;
	text-decoration: none;
	word-wrap: break-word;
	&.sidebar-nav-item {
		&:hover {
			@include link-no-decoration();
		}
		&:focus {
			@include link-no-decoration();
		}
	}
	&:focus {
		@include link-hover();
		&.tag {
			@include link-no-decoration();
		}
	}
	&:hover {
		@include link-hover();
		&.tag {
			@include link-no-decoration();
		}
	}
	&.tag {
		@include link-no-decoration();
	}
	&.read-more-symbol {
		text-decoration: none;
		@include link-no-decoration();
	}
}


.content {
	padding-bottom: 2rem;
	padding-top: 2rem;
}
