// for the list of posts

.section__title {
	font-size: $section__title-font-size;
}

.post-list__item {
	margin-bottom: 3em;
}

.item__title--big {
	display: block;
	font-size: $item__title-big-font-size;
	line-height: 1.25;
}

.item__title--small {
	font-size: 1rem;

	
}

